import React, { useEffect, useState, useRef } from 'react';
import { Unity, useUnityContext } from 'react-unity-webgl';

interface UnityGameProps {
  width?: string | number;
  height?: string | number;
}

// Define the global Unity instance type
declare global {
  interface Window {
    createUnityInstance: any;
  }
}

const UnityGame: React.FC<UnityGameProps> = ({ 
  width = '100%', 
  height = '100%'
}) => {
  const [error, setError] = useState<string | null>(null);
  const [loadingPercentage, setLoadingPercentage] = useState(0);
  const [unityLoaded, setUnityLoaded] = useState(false);
  const mountedRef = useRef(true);
  const containerRef = useRef<HTMLDivElement>(null);

  // Define Unity context props - use process.env.PUBLIC_URL to ensure correct path in any environment
  const contextProps = {
    loaderUrl: `${process.env.PUBLIC_URL}/build/game/Build.loader.js`,
    dataUrl: `${process.env.PUBLIC_URL}/build/game/Build.data`,
    frameworkUrl: `${process.env.PUBLIC_URL}/build/game/Build.framework.js`,
    codeUrl: `${process.env.PUBLIC_URL}/build/game/Build.wasm`,
    companyName: "DefaultCompany",
    productName: "RealmsOfWar",
    productVersion: "1.0",
    // Set canvas dimensions
    webglContextAttributes: {
      preserveDrawingBuffer: true,
    },
  };

  // Debug logs to help troubleshoot
  useEffect(() => {
    console.log("Loading Unity from:", contextProps.loaderUrl);
  }, [contextProps.loaderUrl]);

  // Always call hooks unconditionally at the top level
  const {
    unityProvider,
    isLoaded,
    loadingProgression,
    addEventListener,
    removeEventListener,
    sendMessage,
    unload,
    UNSAFE__detachAndUnloadImmediate: detachAndUnloadImmediate,
  } = useUnityContext(contextProps);

  // Set mounted ref for cleanup
  useEffect(() => {
    return () => {
      mountedRef.current = false;
    };
  }, []);

  // Handle loading progression
  useEffect(() => {
    if (loadingProgression !== undefined) {
      try {
        setLoadingPercentage(Math.round(loadingProgression * 100));
      } catch (err) {
        console.error("Error updating loading progress:", err);
      }
    }
  }, [loadingProgression]);

  // Handle Unity loaded state
  useEffect(() => {
    if (isLoaded) {
      setUnityLoaded(true);
    }
  }, [isLoaded]);
  
  // Handle global errors
  useEffect(() => {
    const handleError = (event: ErrorEvent) => {
      console.error("Unity WebGL Error:", event);
      // Check if error is related to Unity or loading files
      if (event.message && (
        event.message.includes("Unity") || 
        event.message.includes("WebGL") || 
        event.message.includes("wasm") ||
        event.message.includes("promise") ||
        event.filename?.includes("unity") ||
        event.message.includes("Unexpected token")
      )) {
        setError(`Unity WebGL error: ${event.message}\nFile: ${event.filename}`);
      }
    };

    // Handle unhandled promise rejections (common with Unity WebGL)
    const handlePromiseRejection = (event: PromiseRejectionEvent) => {
      console.error("Unhandled Promise Rejection:", event);
      setError(`Unity WebGL loading failed: ${event.reason || "Unknown promise rejection"}`);
    };
    
    window.addEventListener('error', handleError);
    window.addEventListener('unhandledrejection', handlePromiseRejection);
    
    return () => {
      window.removeEventListener('error', handleError);
      window.removeEventListener('unhandledrejection', handlePromiseRejection);
    };
  }, []);
  
  // Handle mobile device optimizations
  useEffect(() => {
    if (/iPhone|iPad|iPod|Android/i.test(navigator.userAgent)) {
      const meta = document.createElement('meta');
      meta.name = 'viewport';
      meta.content = 'width=device-width, height=device-height, initial-scale=1.0, user-scalable=no, shrink-to-fit=yes';
      document.getElementsByTagName('head')[0].appendChild(meta);
      
      document.body.style.textAlign = "left";
      
      return () => {
        if (document.getElementsByTagName('head')[0].contains(meta)) {
          document.getElementsByTagName('head')[0].removeChild(meta);
        }
      };
    }
  }, []);

  // Clean up Unity instance when component unmounts
  useEffect(() => {
    if (!detachAndUnloadImmediate) return;

    return () => {
      try {
        if (mountedRef.current && detachAndUnloadImmediate) {
          detachAndUnloadImmediate();
        }
      } catch (err) {
        console.error("Error while unloading Unity:", err);
      }
    };
  }, [detachAndUnloadImmediate]);

  // Check for WebGL compatibility
  useEffect(() => {
    try {
      const canvas = document.createElement('canvas');
      const gl = canvas.getContext('webgl') || canvas.getContext('experimental-webgl');
      
      if (!gl) {
        setError('WebGL is not supported in your browser. Please try a different browser or update your graphics drivers.');
      }
    } catch (e) {
      setError('Error checking WebGL compatibility: ' + (e instanceof Error ? e.message : 'Unknown error'));
    }
  }, []);

  // Add manual preload for Unity files
  useEffect(() => {
    const preloadFiles = [
      contextProps.loaderUrl,
      contextProps.dataUrl,
      contextProps.frameworkUrl,
      contextProps.codeUrl
    ];

    // Check if files are accessible
    preloadFiles.forEach(url => {
      fetch(url, { method: 'HEAD' })
        .then(response => {
          if (!response.ok) {
            console.error(`File not accessible: ${url}`);
            setError(`Unity file not accessible: ${url}`);
          } else {
            console.log(`File verified: ${url}`);
          }
        })
        .catch(err => {
          console.error(`Error checking file: ${url}`, err);
          setError(`Failed to access Unity file: ${url}`);
        });
    });
  }, [contextProps]);

  return (
    <div 
      ref={containerRef}
      style={{ 
        width: '100%', 
        height: '100%', 
        position: 'relative',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        overflow: 'hidden',
        backgroundColor: '#231F20'
      }}
    >
      {error && (
        <div style={{ 
          color: '#ff6b6b', 
          padding: '1rem', 
          backgroundColor: '#5c0a0a', 
          borderRadius: '4px', 
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          zIndex: 20,
          textAlign: 'center',
          maxWidth: '80%'
        }}>
          <h3>Unity WebGL Error</h3>
          <p style={{ whiteSpace: 'pre-line' }}>{error}</p>
          <div style={{ marginTop: '10px' }}>
            <button 
              onClick={() => window.location.reload()}
              style={{
                backgroundColor: '#ff6b6b',
                color: 'white',
                border: 'none',
                padding: '5px 10px',
                borderRadius: '4px',
                cursor: 'pointer'
              }}
            >
              Try Again
            </button>
          </div>
          <p style={{ fontSize: '12px', marginTop: '10px' }}>
            If this error persists, try using a different browser or check your WebGL compatibility.
          </p>
        </div>
      )}
      
      {!error && !unityLoaded && unityProvider && (
        <div style={{ 
          position: 'absolute', 
          top: '50%', 
          left: '50%', 
          transform: 'translate(-50%, -50%)',
          textAlign: 'center', 
          color: 'white',
          zIndex: 10 
        }}>
          <p>Loading Unity Game... {loadingPercentage}%</p>
          <div style={{
            width: '300px',
            height: '20px',
            backgroundColor: '#444',
            borderRadius: '10px',
            overflow: 'hidden'
          }}>
            <div style={{
              width: `${loadingPercentage}%`,
              height: '100%',
              backgroundColor: '#7289da',
              transition: 'width 0.3s ease-in-out'
            }} />
          </div>
        </div>
      )}
      
      {!error && unityProvider && (
        <Unity 
          unityProvider={unityProvider}
          style={{
            width,
            height,
            visibility: unityLoaded ? 'visible' : 'hidden',
            objectFit: 'contain'
          }}
          devicePixelRatio={1}
          matchWebGLToCanvasSize={true}
        />
      )}
    </div>
  );
};

export default UnityGame; 