import React, { useEffect, useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { collection, addDoc, getDocs, getDoc, doc, updateDoc, deleteDoc, query, where } from 'firebase/firestore';
import { auth, db } from '../../firebase/config';
import NavBar from '../../components/NavBar';
import { Unity, useUnityContext } from "react-unity-webgl";

// Interfaces for data structures
interface AdminUser {
  isAdmin: boolean;
}

interface TileAsset {
  id: string;
  name: string;
  category: string;
  imageUrl: string;
  storageRef: string;
  createdAt: string;
  createdBy: string;
}

interface MapTile {
  type: string;
  x: number;
  y: number;
  base?: string; // Player base indicator (red, blue, green, etc.)
}

interface GameMap {
  id?: string;
  name: string;
  description: string;
  createdAt: Date;
  createdBy: string;
  tiles: MapTile[];
  hexagonal: boolean;
  mapSize: { width: number; height: number };
  playerCount: number;
  previewUrl?: string; // URL to the map preview image
}

const MapEditor: React.FC = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [successMessage, setSuccessMessage] = useState('');
  const [isAdmin, setIsAdmin] = useState(false);
  const [tileAssets, setTileAssets] = useState<TileAsset[]>([]);
  const [mapName, setMapName] = useState<string>('');
  const [mapDescription, setMapDescription] = useState<string>('');
  const [saving, setSaving] = useState(false);
  
  const nameInputRef = useRef<HTMLInputElement>(null);
  const descriptionRef = useRef<HTMLTextAreaElement>(null);

  // Unity context for the map editor
  const { unityProvider, isLoaded, loadingProgression } = useUnityContext({
    loaderUrl: `${process.env.PUBLIC_URL}/build/mapeditor/Build.loader.js`,
    dataUrl: `${process.env.PUBLIC_URL}/build/mapeditor/Build.data`,
    frameworkUrl: `${process.env.PUBLIC_URL}/build/mapeditor/Build.framework.js`,
    codeUrl: `${process.env.PUBLIC_URL}/build/mapeditor/Build.wasm`,
  });

  // Check if user has admin privileges
  useEffect(() => {
    const checkAdminStatus = async () => {
      try {
        const user = auth.currentUser;
        if (!user) {
          navigate('/signin');
          return;
        }

        // Check if the user has admin privileges
        const userDoc = await getDoc(doc(db, 'users', user.uid));
        if (userDoc.exists()) {
          const userData = userDoc.data() as AdminUser;
          if (userData.isAdmin) {
            setIsAdmin(true);
            // Load tile assets
            await loadTileAssets();
          } else {
            setError('You do not have admin privileges');
            setTimeout(() => {
              navigate('/home');
            }, 3000);
          }
        } else {
          setError('User data not found');
          setTimeout(() => {
            navigate('/home');
          }, 3000);
        }
      } catch (err: any) {
        setError(err.message || 'Error checking admin status');
      } finally {
        setLoading(false);
      }
    };

    checkAdminStatus();
  }, [navigate]);

  const loadTileAssets = async () => {
    try {
      const tileAssetsCollection = collection(db, 'tileAssets');
      const snapshot = await getDocs(tileAssetsCollection);
      const assets: TileAsset[] = [];
      
      snapshot.forEach(doc => {
        assets.push({
          id: doc.id,
          ...doc.data() as Omit<TileAsset, 'id'>
        });
      });
      
      // Group by category
      const groupedAssets = assets.reduce((acc, asset) => {
        if (!acc[asset.category]) {
          acc[asset.category] = [];
        }
        acc[asset.category].push(asset);
        return acc;
      }, {} as Record<string, TileAsset[]>);
      
      // Flatten and sort
      const sortedAssets: TileAsset[] = [];
      ['terrain', 'resource', 'decoration', 'structure', 'unit'].forEach(category => {
        if (groupedAssets[category]) {
          sortedAssets.push(...groupedAssets[category]);
        }
      });
      
      setTileAssets(sortedAssets);
    } catch (err: any) {
      console.error('Error loading tile assets:', err);
      setError('Error loading tile assets');
    }
  };

  const handleSaveMap = async () => {
    try {
      if (!mapName.trim()) {
        setError('Please enter a map name');
        nameInputRef.current?.focus();
      return;
    }
    
    setSaving(true);
      
      // Create a map object - Note: mapSize and playerCount will be set by Unity
      const mapData: Omit<GameMap, 'id'> = {
        name: mapName,
        description: mapDescription,
        createdAt: new Date(),
        createdBy: auth.currentUser?.uid || '',
        tiles: [], // Tiles will be set by Unity
        hexagonal: true,
        mapSize: { width: 0, height: 0 }, // Will be set by Unity
        playerCount: 0, // Will be set by Unity
      };
      
      // Save the map to Firestore
      const mapsCollection = collection(db, 'maps');
      await addDoc(mapsCollection, mapData);
      
      // Show success message
      setSuccessMessage('Map metadata saved successfully! Please complete the map creation in Unity.');
      
      // Clear success message after a delay
      setTimeout(() => {
        setSuccessMessage('');
      }, 3000);
      
    } catch (err: any) {
      console.error('Error saving map:', err);
      setError(err.message || 'Error saving map');
    } finally {
      setSaving(false);
    }
  };

  const handleMapNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setMapName(e.target.value);
  };

  const handleDescriptionChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setMapDescription(e.target.value);
  };

  if (loading) {
    return (
      <div style={{ 
        minHeight: '100vh', 
        backgroundColor: '#1a1a2e', 
        color: 'white',
        display: 'flex',
        flexDirection: 'column' 
      }}>
        <NavBar />
        <div style={{ 
          flex: 1,
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          flexDirection: 'column',
          padding: '2rem'
        }}>
          <p>Loading Map Editor...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div style={{ 
        minHeight: '100vh', 
        backgroundColor: '#1a1a2e', 
        color: 'white',
        display: 'flex',
        flexDirection: 'column' 
      }}>
        <NavBar />
        <div style={{ 
          flex: 1,
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          flexDirection: 'column',
          padding: '2rem'
        }}>
          <div style={{ 
            backgroundColor: '#5c0a0a', 
            color: '#ff6b6b', 
            padding: '1.5rem',
            borderRadius: '8px',
            maxWidth: '600px',
            textAlign: 'center'
          }}>
            <p>{error}</p>
          </div>
        </div>
      </div>
    );
  }

  if (!isAdmin) {
    return (
      <div style={{ 
        minHeight: '100vh', 
        backgroundColor: '#1a1a2e', 
        color: 'white',
        display: 'flex',
        flexDirection: 'column' 
      }}>
        <NavBar />
        <div style={{ 
          flex: 1,
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          flexDirection: 'column',
          padding: '2rem'
        }}>
          <div style={{ 
            backgroundColor: '#5c0a0a', 
            color: '#ff6b6b', 
            padding: '1.5rem',
            borderRadius: '8px',
            maxWidth: '600px',
            textAlign: 'center'
          }}>
            <p>You do not have admin privileges. Redirecting...</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div style={{ 
      minHeight: '100vh', 
      backgroundColor: '#1a1a2e', 
      color: 'white',
      display: 'flex',
      flexDirection: 'column' 
    }}>
      <NavBar />
      
      <div style={{ 
        flex: 1,
        padding: '2rem',
        display: 'flex',
        flexDirection: 'column'
      }}>
        <div style={{ 
          display: 'flex', 
          justifyContent: 'space-between', 
          alignItems: 'center',
          marginBottom: '2rem'
        }}>
          <h1>Map Editor</h1>
          <div>
          <button 
              onClick={() => navigate('/admin')}
            style={{ 
              backgroundColor: '#4a4a8f',
              color: 'white',
              border: 'none',
              padding: '0.5rem 1rem',
              borderRadius: '4px',
                marginRight: '0.5rem',
                cursor: 'pointer'
            }}
          >
              Back to Dashboard
          </button>
            <button
              onClick={handleSaveMap}
              disabled={saving}
              style={{ 
                backgroundColor: '#4caf50',
                color: 'white',
                border: 'none',
                padding: '0.5rem 1rem',
            borderRadius: '4px',
                cursor: saving ? 'not-allowed' : 'pointer',
                opacity: saving ? 0.7 : 1
              }}
            >
              {saving ? 'Saving...' : 'Save Map'}
            </button>
          </div>
        </div>
        
        {successMessage && (
          <div style={{ 
            backgroundColor: '#0a5c0a', 
            color: '#6bff6b', 
            padding: '1rem', 
            borderRadius: '8px',
            marginBottom: '1.5rem',
            textAlign: 'center'
          }}>
            <p>{successMessage}</p>
          </div>
        )}
        
        <div style={{ 
          display: 'grid',
          gridTemplateColumns: '300px 1fr',
          gap: '1.5rem' 
        }}>
          <div>
            <div style={{ backgroundColor: '#262640', borderRadius: '8px', padding: '1.5rem', marginBottom: '1.5rem' }}>
              <h2 style={{ marginBottom: '1.5rem' }}>Map Properties</h2>
              
              <div style={{ marginBottom: '1rem' }}>
                <label style={{ display: 'block', marginBottom: '0.5rem' }}>
                  Map Name
                </label>
                <input
                  ref={nameInputRef}
                  type="text"
                  value={mapName}
                  onChange={handleMapNameChange}
                  style={{ 
                    width: '100%', 
                    padding: '0.5rem',
                    backgroundColor: '#1a1a2e', 
                    color: 'white',
                    border: '1px solid #4a4a8f',
                    borderRadius: '4px'
                  }}
                  placeholder="Enter map name"
                />
              </div>
              
              <div style={{ marginBottom: '1rem' }}>
                <label style={{ display: 'block', marginBottom: '0.5rem' }}>
                  Description
                </label>
                <textarea
                  ref={descriptionRef}
                  value={mapDescription}
                  onChange={handleDescriptionChange}
                  style={{ 
                    width: '100%', 
                    padding: '0.5rem',
                    backgroundColor: '#1a1a2e', 
                    color: 'white',
                    border: '1px solid #4a4a8f',
                    borderRadius: '4px',
                    minHeight: '100px',
                    resize: 'vertical'
                  }}
                  placeholder="Enter map description"
                />
                  </div>
                </div>
              </div>
              
          <div>
            <div style={{ backgroundColor: '#262640', borderRadius: '8px', padding: '1.5rem' }}>
              <h2 style={{ marginBottom: '1.5rem' }}>Map Editor</h2>
              
              {!isLoaded && (
                <div style={{ 
                  textAlign: 'center', 
                  padding: '2rem', 
                  backgroundColor: '#1a1a2e', 
                        borderRadius: '4px',
                  marginBottom: '1rem'
                }}>
                  <p>Loading Unity Map Editor... ({Math.round(loadingProgression * 100)}%)</p>
              </div>
            )}
            
              <Unity 
                unityProvider={unityProvider}
                style={{
                  width: '100%',
                  height: '720px',
                  backgroundColor: '#1a1a2e',
                  borderRadius: '4px',
                  visibility: isLoaded ? 'visible' : 'hidden'
                }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MapEditor; 