import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { doc, getDoc, updateDoc } from 'firebase/firestore';
import { auth, db } from '../firebase/config';
import NavBar from '../components/NavBar';
import UnityGame from '../components/UnityGame';

interface PlayerInfo {
  id: string;
  displayName: string;
}

const GameScene: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [gameData, setGameData] = useState<any>(null);
  const [showGameInfo, setShowGameInfo] = useState(false);
  const [players, setPlayers] = useState<PlayerInfo[]>([]);
  
  // No need for Unity loading state variables since iframe handles it internally

  useEffect(() => {
    const user = auth.currentUser;
    if (!user) {
      navigate('/signin');
      return;
    }

    if (!id) return;

    const fetchGame = async () => {
      try {
        const gameDoc = await getDoc(doc(db, 'games', id));
        if (gameDoc.exists()) {
          const data = gameDoc.data();
          
          if (!data.players.includes(user.uid)) {
            setError('You are not a player in this game');
            setLoading(false);
            return;
          }

          setGameData({ id: gameDoc.id, ...data });
          
          // Fetch player information
          const playerInfoPromises = data.players.map(async (playerId: string) => {
            try {
              const userDoc = await getDoc(doc(db, 'users', playerId));
              if (userDoc.exists()) {
                const userData = userDoc.data();
                return {
                  id: playerId,
                  displayName: userData.displayName || 'Unknown Player'
                };
              } else {
                return {
                  id: playerId, 
                  displayName: playerId === user.uid ? 'You' : `Player (${playerId.substring(0, 4)}...)`
                };
              }
            } catch (error) {
              console.error(`Error fetching user ${playerId}:`, error);
              return {
                id: playerId,
                displayName: playerId === user.uid ? 'You' : `Player (${playerId.substring(0, 4)}...)`
              };
            }
          });
          
          const playerInfos = await Promise.all(playerInfoPromises);
          setPlayers(playerInfos);
          
          setLoading(false);
        } else {
          setError('Game not found');
          setLoading(false);
        }
      } catch (err: any) {
        setError(err.message || 'Error loading game');
        setLoading(false);
      }
    };

    fetchGame();
  }, [id, navigate]);

  const handleEndTurn = async () => {
    if (!gameData || !id) return;
    
    try {
      const currentPlayerIndex = gameData.players.indexOf(auth.currentUser?.uid);
      const nextPlayerIndex = (currentPlayerIndex + 1) % gameData.players.length;
      const nextPlayerId = gameData.players[nextPlayerIndex];

      await updateDoc(doc(db, 'games', id), {
        currentTurn: nextPlayerId,
        lastUpdated: new Date()
      });

      // Refresh game data
      const gameDoc = await getDoc(doc(db, 'games', id));
      if (gameDoc.exists()) {
        setGameData({ id: gameDoc.id, ...gameDoc.data() });
      }
    } catch (error) {
      console.error('Error ending turn:', error);
    }
  };

  const handleSurrender = async () => {
    if (!gameData || !id || !auth.currentUser) return;
    
    if (window.confirm('Are you sure you want to surrender this game?')) {
      try {
        // Remove player from the game
        const updatedPlayers = gameData.players.filter(
          (playerId: string) => playerId !== auth.currentUser?.uid
        );
        
        await updateDoc(doc(db, 'games', id), {
          players: updatedPlayers,
          // If it was this player's turn, move to next player
          ...(gameData.currentTurn === auth.currentUser.uid 
            ? { currentTurn: updatedPlayers[0] || '' } 
            : {}),
          lastUpdated: new Date()
        });

        navigate('/home');
      } catch (error) {
        console.error('Error surrendering game:', error);
      }
    }
  };

  if (loading) {
    return (
      <div style={{ minHeight: '100vh', backgroundColor: '#1a1a2e', color: 'white' }}>
        <NavBar />
        <div style={{ padding: '2rem', textAlign: 'center' }}>
          <p>Loading game...</p>
        </div>
      </div>
    );
  }

  if (error || !gameData) {
    return (
      <div style={{ minHeight: '100vh', backgroundColor: '#1a1a2e', color: 'white' }}>
        <NavBar />
        <div style={{ padding: '2rem', textAlign: 'center' }}>
          <p style={{ color: '#ff6b6b' }}>{error || 'Game not found'}</p>
          <button 
            onClick={() => navigate('/home')}
            style={{ 
              backgroundColor: '#4a4a8f', 
              color: 'white',
              border: 'none',
              padding: '0.75rem 1.5rem',
              borderRadius: '4px',
              cursor: 'pointer',
              marginTop: '1rem'
            }}
          >
            Back to Home
          </button>
        </div>
      </div>
    );
  }

  const isMyTurn = gameData.currentTurn === auth.currentUser?.uid;
  
  // Find current player's name
  const currentPlayerInfo = players.find(player => player.id === gameData.currentTurn);
  const currentPlayerName = currentPlayerInfo ? currentPlayerInfo.displayName : 'Unknown Player';

  return (
    <div style={{ 
      minHeight: '100vh', 
      backgroundColor: '#1a1a2e', 
      color: 'white',
      display: 'flex',
      flexDirection: 'column'
    }}>
      <NavBar />
      
      <div style={{ padding: '1rem', display: 'flex', flexDirection: 'column', flexGrow: 1 }}>
        <h1 style={{ marginBottom: '1rem' }}>{gameData.name}</h1>
        
        {/* Game controls */}
        <div style={{ 
          padding: '1.5rem',
          backgroundColor: '#262640',
          borderRadius: '8px',
          marginBottom: '1.5rem'
        }}>
          <div style={{ 
            display: 'flex',
            justifyContent: 'space-between',
            marginBottom: '1.5rem',
            flexWrap: 'wrap',
            gap: '1rem'
          }}>
            <div>
              <strong>Current Turn:</strong> 
              <span style={{ 
                color: isMyTurn ? '#4CAF50' : '#ff9800', 
                fontWeight: 'bold', 
                marginLeft: '0.5rem' 
              }}>
                {currentPlayerName}
              </span>
            </div>
            <div><strong>Players:</strong> {gameData.players.length}/{gameData.maxPlayers}</div>
          </div>
          
          <div style={{ 
            display: 'flex',
            gap: '1rem',
            marginBottom: '1rem',
            flexWrap: 'wrap'
          }}>
            <button 
              onClick={handleEndTurn}
              disabled={!isMyTurn}
              style={{ 
                backgroundColor: isMyTurn ? '#4CAF50' : '#4a4a8f', 
                color: 'white',
                border: 'none',
                padding: '0.75rem 1.5rem',
                borderRadius: '4px',
                cursor: isMyTurn ? 'pointer' : 'not-allowed',
                opacity: isMyTurn ? 1 : 0.7,
                fontWeight: 'bold',
                fontSize: '1rem'
              }}
            >
              End Turn
            </button>
            
            <button 
              onClick={() => setShowGameInfo(!showGameInfo)}
              style={{ 
                backgroundColor: showGameInfo ? '#ff6b35' : '#4a4a8f', 
                color: 'white',
                border: 'none',
                padding: '0.75rem 1.5rem',
                borderRadius: '4px',
                cursor: 'pointer',
                fontWeight: 'bold',
                fontSize: '1rem'
              }}
            >
              Game Info
            </button>
            
            <button 
              onClick={handleSurrender}
              style={{ 
                backgroundColor: '#f44336', 
                color: 'white',
                border: 'none',
                padding: '0.75rem 1.5rem',
                borderRadius: '4px',
                cursor: 'pointer',
                fontWeight: 'bold',
                fontSize: '1rem'
              }}
            >
              Surrender
            </button>
          </div>
        </div>
        
        {/* Unity Game Container */}
        <div style={{ 
          backgroundColor: '#262640',
          borderRadius: '8px',
          overflow: 'hidden',
          position: 'relative',
          width: '100%',
          height: '720px',
          margin: '0 auto',
          display: 'flex',
          boxShadow: '0 4px 8px rgba(0, 0, 0, 0.3)',
          border: '2px solid #333'
        }}>
          <UnityGame
            width="100%"
            height="100%"
          />
        </div>
        
        {/* Game Info Section */}
        {showGameInfo && (
          <div style={{ marginBottom: '1.5rem' }}>
            <div style={{
              backgroundColor: '#262640',
              borderRadius: '8px',
              padding: '1.5rem'
            }}>
              <h3 style={{ marginBottom: '1rem' }}>Game Information</h3>
              
              <div style={{ marginBottom: '1rem' }}>
                <strong>Map:</strong> {gameData.mapName || 'Unknown Map'}
              </div>
              
              <div style={{ marginBottom: '1rem' }}>
                <strong>Created:</strong> {gameData.createdAt ? new Date(gameData.createdAt.toDate()).toLocaleString() : 'Unknown'}
              </div>
              
              <div style={{ marginBottom: '1rem' }}>
                <strong>Game ID:</strong> {gameData.id}
              </div>
              
              <h4 style={{ marginTop: '1.5rem', marginBottom: '0.5rem' }}>Players:</h4>
              <ul style={{ listStyleType: 'none', padding: 0 }}>
                {players.map((player, index) => (
                  <li key={player.id} style={{ 
                    marginBottom: '0.5rem',
                    color: gameData.currentTurn === player.id ? '#4CAF50' : 'inherit',
                    fontWeight: gameData.currentTurn === player.id ? 'bold' : 'normal'
                  }}>
                    Player {index + 1}: {player.displayName}
                    {gameData.currentTurn === player.id && ' (Current Turn)'}
                  </li>
                ))}
              </ul>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default GameScene; 